@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Aleo&display=swap");

body {
  background-color: hsl(0, 0%, 10%) !important;
  color: white;
}

.font-logo {
  font-family: "Aleo", serif;
}

.font-custom {
  font-family: "Open Sans", sans-serif;
}

.select-none {
  user-select: none;
}

.bg-primary {
  background-color: hsl(0, 0%, 13%) !important;
}

.bg-secondary {
  background-color: hsl(0, 0%, 10%) !important;
}

.bg-ternary {
  background-color: hsl(0, 0%, 17%) !important;
}

.mx-10 {
  margin: 0rem 8rem !important;
}

.my-10 {
  margin: 8rem 0rem !important;
}

.ml-10 {
  margin: 0rem 0rem 0rem 9rem !important;
}

.mr-10 {
  margin: 0rem 10rem 0rem 0rem !important;
}

.px-10 {
  padding: 1rem 7rem !important;
}

.py-10 {
  padding: 8rem 1rem !important;
}

.pl-10 {
  padding: 0rem 0rem 0rem 8rem !important;
}

.pr-10 {
  padding: 0rem 8rem 0rem 0rem !important;
}

.w-90 {
  width: 90%;
}

.width-custom {
  width: 65%;
}

@media only screen and (min-width: 768px) {
  .languages {
    margin-left: auto !important;
    margin-right: auto !important;
  }
}

@media only screen and (max-width: 768px) {
  .mx-10 {
    margin: 0 !important;
  }

  .my-10 {
    margin: 0 !important;
  }

  .ml-10 {
    margin: 0 !important;
  }

  .mr-10 {
    margin: 0 !important;
  }

  .px-10 {
    padding: 1rem 5rem !important;
  }

  .py-10 {
    padding: 8 !important;
  }

  .pl-10 {
    padding: 0 !important;
  }

  .pr-10 {
    padding: 0 !important;
  }

  .display-none {
    display: none;
  }

  .width-custom {
    width: 100%;
  }
}

.cursor-pointer {
  cursor: pointer;
}

.cursor-default {
  cursor: default;
}

.cw-logo {
  max-height: 60px;
  max-width: 60px;
}

.fn-title {
  font-weight: 600;
  font-size: 1.2rem;
}

.logoSmall {
  max-width: 55px;
  max-height: 30px;
}

.logoLarge {
  max-height: auto;
  max-width: auto;
}

.min-vh-custom {
  min-height: 70vh;
}
